import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home')
  },
  {
    path: '/quizz',
    name: 'Quizz',
    component: () => import('../views/Quizz')
  }
]

const router = new VueRouter({
  routes
})

export default router
